// 引入axios
import axios from "axios";
import Vue from "vue";

// 创建axios实例
const api=axios.create({
    baseURL:"http://sandbox_api.estudy.chanke.xyz",
})
// withCredentials  跨域访问需要发送cookie时一定要加axios.defaults.withCredentials = true;
api.defaults.withCredentials=true;
// 给所有的post请求配置headers，设置Content-Type的值
api.defaults.headers.post["Content-Type"]="application/json"

//拦截器
api.interceptors.response.use(response=>{
    return response.data.data;

},error=>{
    const config=error.config;
    // error.config存储的是请求的配置
    // 如果其中没有_slient这个自定义属性，就说明请求没有成功，此时才在拦截器中发出一个错误通知
    if(!config._slient){
        const response=error.response;
        // 引入了vue，因为vue中有$notification
        // $notification.error打开一个错误提示框
        // error的参数是一个对象，message通知提醒标题，必选，description	通知提醒内容，必选
        Vue.$notification.error({
            message:"API ERROR",
            description:response.data.errorMessage,
    
        })
    }
    // 默认是返回undefined(没有任何返回值是返回undefined),所以是返回一个非promise对象，导致请求后返回的状态是resolve状态，所以导致链式调用会走成功的回调函数
    // 所以，失败的话，return Promise.reject(error)，返回是一个Promise对象的话，请求后返回的状态就根据Promise对象的状态走，所以给这个Promise对象设置设置成reject状态，并把error传进去
    return Promise.reject(error)
})
export default api







