<template>
  <div id="teacher">
    <div>
      <a-table
        :rowKey="assignments=>assignments.assignment_id"
        :dataSource="teacher.assignments"
        :columns="columns"
        :pagination="false"
      >
        <template slot="operation" slot-scope="text, record">
          <div v-if="record.work_count>0">
            <a-button
              v-if="!revisingAssignment"
              icon="plus-square"
              type="primary"
              @click="revisingAssignment=record"
            >展开</a-button>
            <a-button v-else icon="minus-square" type="primary" @click="revisingAssignment=false">收起</a-button>

            <a :href="`${downloadHost}All?id=${record.assignment_id}`">
              <a-button icon="download">下载全部</a-button>
            </a>

            
          </div>
          <div v-else>
            <a-popconfirm
              title="确认删除该作业吗?"
              @confirm="removeAssignment(record.assignment_id)"
              okText="确定"
              cancelText="取消"
            >
              <a-button icon="delete" type="danger">删除</a-button>
            </a-popconfirm>
          </div>
        </template>
      </a-table>
      <!-- 点击展开后显示的table -->
      <a-table
        v-if="revisingAssignment"
        :rowKey="assignments=>assignments.user.name"
        :dataSource="revisingAssignment.works"
        :columns="workColumns"
      >
        <template slot="operation" slot-scope="text,record">
          <a :href="`${downloadHost}?id=${record.id}&type=student`">
            <a-button icon="download">下载</a-button>
          </a>
          <!-- 点击批改时使revisingWork=record，这样revisingWork技能转为布尔值true使modal框显示，也能使revisingWork存储record -->
          <a-button
            icon="form"
            type="primary"
            @click="revisingWork=record;revisingForm.review=record.teacher_review"
          >批改</a-button>
        </template>
      </a-table>

      <!-- 点击批改后显示的Modal对话框 -->
      <!-- Modal 对话框 visible控制显示与否-->
      <!-- footer为null时 Modal 对话框的footer没有 -->
      <a-modal
        :visible="Boolean(revisingWork)"
        @cancel="revisingWork=false"
        @ok="revisingWork=false"
        :footer="null"
      >
        <form action>
          <!-- 用a-form-item标签包裹，这个标签有一个label属性，这个标签类似于label标签 -->
          <a-form-item label="状态">
            <!-- v-model双向数据绑定，选择的值会被保存到revisingForm.status中 -->
            <a-select v-model="revisingForm.status">
              <!-- a-select-option是选项。使用v-for循环work_status -->
              <!-- status_value为属性值，status为属性名,index为索引-->
              <!-- status属性名为0/1/2，因为这个才是要提交到后台的数据，所以value绑定它 -->
              <a-select-option
                v-for="(status_value,status,index) in work_status"
                :key="index"
                :value="status"
              >{{status_value}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="批改意见">
            <!-- 文本框中的值绑定到revisingForm.review -->
            <!-- autosize属性会使文本框根据内容多少自动匹配大小 -->
            <a-textarea v-model="revisingForm.review" autosize></a-textarea>
          </a-form-item>
          <a-form-item label="附件">
            <!-- 上传按钮，actio绑定uploadHost，上传的东西会传到这个地址 -->
            <!-- 上传的接口还要求把Token传过去，通过:headers传 -->
            <!-- 这个Token是info里面的token，所以要获取info -->
            <!-- 还要传id过去,通过:data传 -->
            <a-upload
              :action="uploadHost"
              :headers="{Token:info.token}"
              :data="{id:revisingWork.id}"
            >
              <a-button icon="upload">上传</a-button>
            </a-upload>
          </a-form-item>
          <a-form-item>
            <a-button type="primary" @click="save">保存</a-button>
          </a-form-item>
        </form>
      </a-modal>

      <!-- 点击新建作业后显示modal框 -->
      <a-modal
        :visible="teacher.homeworkVisible"
        @cancel="teacher.homeworkVisible=false"
        :footer="null"
      >
        <h4>新建作业</h4>
        <form action>
          <a-form-item label="课程">
            <!-- v-model双向数据绑定，选择的值会被保存到homework.org_id中 -->
            <a-select v-model="homework.org_id">
              <a-select-option
                v-for="(orgs_value,orgs,index) in teacher.orgs"
                :key="index"
                :value="orgs_value.id"
              >{{orgs_value.full_name}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="作业名称">
            <a-textarea v-model="homework.name" autosize></a-textarea>
          </a-form-item>
          <a-form-item label="开始/结束时间">
            <!-- 日期选择框 -->
            <!-- change事件是 日期范围发生变化的回调 -->
            <!-- 使用 format 属性，可以自定义日期显示格式。 -->
            <a-range-picker :format="dateFormat" @change="onChange" />
          </a-form-item>
          <a-form-item>
            <a-button type="primary" @click="saveHomework">保存</a-button>
          </a-form-item>
        </form>
      </a-modal>
      
    </div>
  </div>
</template>

<script>
// api暴露时不是用{api}暴露，所以引入时不用{api}引入
import api from "../utils/api";
import { mapState } from "vuex";

const work_status = {
  "0": "待批改",
  "1": "需完善",
  "2": "已完成"
};
const Host = "http://sandbox_api.estudy.chanke.xyz";

export default {
  props: ["teacher"],
  methods: {
    // 日期选择框回调函数，日期范围发生变化的回调
    // 第一个参数date为一个长度为2的数组，它是开始日期和截止日期的两个对象，包含日期的更多信息(包含_d,_isAMomentObject,_isUTC,_locale等属性)
    // 第二个参数dateString为一个长度为2的数组，它是开始日期和截止日期的两个字符串
    onChange(date, dateString) {
      this.homework.start_time = dateString[0];
      this.homework.end_time = dateString[1];
    },
    // removeAssignment点击确认删除作业的回调
    removeAssignment(id) {
      api.post("/teacher/deleteAssignment", { id: id }).then(
        response => {
          //  寻找删除的那一项的索引
          const index = this.teacher.assignments.findIndex(assignment => {
            return assignment.assignment_id === id;
          });
          this.teacher.assignments.splice(index, 1);
          this.$message.info("删除成功!");
        },
        error => {}
      );
    },
    // save是点击批改modal框保存按钮的回调
    save() {
      const form = {
        id: this.revisingWork.id,
        review: this.revisingForm.review,
        status: this.revisingForm.status
      };
      // finally是不管请求成功还是失败都要执行的回调函数
      api
        .post("/teacher/review", form)
        .then(
          response => {
            // Object.entries() 方法返回一个给定对象自身可枚举属性的键值对数组
            // 例:对象{ foo: 'bar', baz: 'abc' }会变成 [['foo', 'bar'], ['baz', 'abc']]
            // console.log()
            Object.entries(response).forEach(item => {
              // 把当前的本地数据revisingWork替换成最新的数据
              return this.$set(this.revisingWork, item[0], item[1]);
            });
          },
          error => {}
        )
        .finally(() => {
          // 令revisingWork=false，使modal框隐藏掉
          this.revisingWork = false;
        });
    },
    // saveHomework是点击新建作业modal框的保存按钮的回调
    saveHomework() {
      const form = {
        org_id: this.homework.org_id,
        name: this.homework.name,
        start_time: this.homework.start_time,
        end_time: this.homework.end_time
      };
      api
        .post("/teacher/createAssignment", form)
        .then(
          response => {
            this.$emit("loadDetails")
          },
          error => {console.log(error)}
        )
        .finally(() => {
          this.teacher.homeworkVisible = false;
        });
    }
  },

  data() {
    return {
      //  dateFormat设置日期格式
      dateFormat: "YYYY-MM-DD",
      // work_status本是写在外边的数据，现在写在组件的数据当中，这样在组件当中才能使用它
      work_status,
      revisingWork: false,
      // revisingForm用于 点击批改后展示的表单，用于保存表单的信息
      revisingForm: {
        status: "1",
        review: ""
      },
      // homework用于保存新建作业后展示的表单的信息
      homework: {
        // org_id是课程，默认为'4',即一期实战班
        org_id: "4",
        // name是作业名称
        name: "",
        // start_time开始时间
        start_time: "",
        // end_time结束时间
        end_time: ""
      },
      // 教师下载作业附件
      downloadHost: Host + "/teacher/download",
      // 教师上传作业批改附件
      uploadHost: Host + "/teacher/upload",
      // revisingAssignment用于控制显示点击展开的表格
      revisingAssignment: false,
      //   workColumns点击展开的表格列
      workColumns: [
        {
          title: "学员姓名",
          dataIndex: "user.full_name"
        },
        {
          title: "学号",
          dataIndex: "user.name"
        },
        {
          title: "提交的作业名称",
          dataIndex: "student_upload_name"
        },
        {
          title: "提交时间",
          dataIndex: "commit_time"
        },
        {
          title: "状态",
          customRender(text, record, index) {
            return work_status[record.status];
          }
        },
        {
          title: "操作",
          scopedSlots: {
            customRender: "operation"
          }
        }
      ]
    };
  },

  //   columns写到计算属性当中，因为表格使用了筛选，需要实时监控columns这个数据
  computed: {
    // 通过辅助函数mapState获取info
    ...mapState("user", ["info"]),
    columns() {
      return [
        {
          title: "课程",
          dataIndex: "org_name"
        },
        {
          title: "作业名称",
          dataIndex: "name"
        },
        {
          title: "开始 -- 截至时间",
          customRender(text, record, index) {
            // text当前行的值
            // record当前行的数据
            //index当前行的索引
            return `${record.start_time} -- ${record.end_time}`;
          }
        },
        {
          title: "状态",
          customRender(text, record, index) {
            return (
              `${record.student_count}个学员/${record.work_count}个提交` +
              (record.work_count > 0 ? ` -${record.work_update_time}` : "")
            );
          }
        },
        {
          title: "操作",
          scopedSlots: {
            // scope的名字operation,去渲染operation这个插槽
            customRender: "operation"
          },
          //   如果使用可控的筛选和排序，那么务必指定 column.key
          key: "operation",
          //   filteredValue 筛选的受控属性，外界可用此控制列的筛选状态，值为已筛选的 value 数组
          //  点击展开，revisingAssignment=record，filteredValue有值了，就会进行onFilter回调函数的执行
          // 点击收起，revisingAssignment=false，filteredValue就会为null
          filteredValue: this.revisingAssignment
            ? [this.revisingAssignment.assignment_id]
            : null,

          // onFilter 本地模式下，确定筛选的运行函数, 使用 template 或 jsx 时作为filter事件使用
          // 对每一行进行筛选，筛选的规则是点击哪一行的展开就筛选只留下哪一行
          onFilter(value, record) {
            return value == record.assignment_id;
          }
        }
      ];
    }
  }
};
</script>

