<template>
  <!-- ant design提供了layout，协助进行页面级整体布局。 -->
  <!-- 使用最基本的『上-中-下』布局。 -->
  <a-layout id="app">
    <!-- 首部 -->
    <a-layout-header class="header flexrow">
      <h2>作业系统</h2>
      <div class="right flexrow" v-if="loggedIn">
        <!-- 是老师登录时的头部 -->
        <div v-if="isTeacher" class="flexrow">
          <h4>{{info.name}}</h4>
          <!-- type="primary"使变成蓝色 -->
          <a-button icon="file-add" type="primary" class="vcenter" @click="teacher.homeworkVisible=true">新建作业</a-button>
        </div>
        <!-- 是学生登录时的头部 -->
        <div v-else class="flexrow">
          <h4 class="flexcol">
            <span>{{info.full_name}}</span>
            <span>学号:{{info.name}}</span>
          </h4>
          <h5 class="flexcol">
            <!-- es6语法字符串拼接，使用${}拼接变量 -->
            <span v-for="(org,index) in student.orgs" :key="`${org}_${index}`">{{org}}</span>
          </h5>
          <div class="stats">
            <span>{{student.stats.uncommitted}}个作业待提交</span>
            <span>{{student.stats.revising}}个作业待批改</span>
            <span>{{student.stats.improvable}}个作业需完善</span>
            <span>{{student.stats.finished}}个作业已完成</span>
          </div>
        </div>
        <a href="http://vipgit.chanke.xyz" target="_blank">
        <!-- icon属性为按钮加上小图标，属性值是图标名称 -->
          <a-button icon="home">代码仓库</a-button>
        </a>
        <a-button icon="logout" class="vcenter" @click="$store.dispatch('user/logout')">退出</a-button>
        <!-- 点击退出按钮时，就调用user中的logout方法 -->
        <!-- $store 是挂载在 Vue 实例上的 -->
      </div>
    </a-layout-header>
    <!-- 内容区 -->
    <a-layout-content>
      <Authorization v-if="!loggedIn"></Authorization>
      <div v-else>
        <!-- 通过自定义属性，父组件往子组件当中传参 -->
        <app-teacher v-if="isTeacher" :teacher="teacher" @loadDetails="loadDetails"></app-teacher>
        <app-student v-else :student="student" @loadDetails="loadDetails"></app-student>
      </div>
    </a-layout-content>
    <!-- 脚部 -->
    <a-layout-footer style="text-align: center">
      eStudy ©2020 Created by LongTeng
    </a-layout-footer>
  </a-layout>
</template>

<script>
import { mapState } from "vuex";
import Authorization from "./components/authorization";
import teacher from "./components/teacher";
import student from "./components/student";
import api from "./utils/api";

export default {
  name: "app",
  components: {
    Authorization,
    "app-teacher": teacher,
    "app-student": student
  },
  data() {
    return {
      // 学生的信息
      student: {
        orgs: [],
        stats: {},
        assignments: []
      },
      // 老师的信息
      teacher: {
        orgs: [],
        assignments: [],
        // homeworkVisible 新建作业modal框显示控制
        homeworkVisible:false,
      }
    };
  },
  computed: {
    //  计算属性：这个属性名(在这里是函数名，如lisTeacher)的值，由其他数据的值决定，其他数据的值发生变化时，它的值才会发生相应的改变
    //使用...扩展运算符
    // 使用常规的方法获取store中的loggedIn，应使用：return this.$store.state.user.logedIn,因为这是去获取state下的user中的数据
    //使用vuex的辅助函数mapState获取user中的info和loggedIn，所以第一个参数是"user"
    ...mapState("user", ["info", "loggedIn"]),
    isTeacher() {
      return this.info.is_admin > 0;
    }
  },
  watch: {
    // 监听器属性：这个属性名(在这里是函数名，如loogedIn)的值发生变化时，就执行相应的操作
    // 但是刷新页面的时候，loggedIn的值并没有发生变化，所以此时不会执行相应的操作，所以刷新页面时，需要在钩子函数去执行操作
    loggedIn(val) {
      console.log(val)
      // val是loggendIn当前的值(watch监听属性中的函数的形参，是这个属性的当前值)
      // 例如：loggedIn当前的值是true，那么val传进来，它就是true
      if (val) {
        this.loadDetails();
      }
    }
  },
  // 一刷新页面就会执行vue的钩子函数
  created() {
    if (this.loggedIn) {
      this.loadDetails();
    }
  },
  methods: {
    
    // loadDetails方法用来请求用户的信息
    loadDetails() {
      if (this.isTeacher) {
        // 这个请求要求在headers中传token，在user.js中已经加上Token首部了，在这里不用再加了
        api.get("/teacher/detail").then(
          response => {
            this.teacher.orgs = response.orgs;
            this.teacher.assignments = response.assignments;
            
          },
          err => {}
        );
      } else {
        api.get("/student/detail").then(
          response => {
            this.student.orgs = response.orgs;
            this.student.stats = response.stats;
            this.student.assignments = response.assignments;
            
          },
          err => {}
        );
      }
    }
  }
};
</script>

