  display: flex;
  // flex-direction: row;flex容器主轴方向，从左向右(默认值)
  flex-direction: row;
  // flex-wrap: nowrap;不换行(单行，默认值)
  flex-wrap: nowrap;
}
.right {
  margin-left: auto;
}
.stats span {
  padding: 0 5px;
}
.flexcol {
  height: 100%;
  display: flex;
  flex-direction: column;
