import Vue from 'vue'
import App from './App.vue'
import store from './store'


// 引入ant design组件库(这样的坏处是引入的东西比较多，刚开始引入的时候会比较卡)
import Antd from "ant-design-vue"
// ant design组件库共有57个组件(目前为止),所以也可只引入其中的一个组件，例如如下只引入 notification(通知提醒框) 组件
import Notification from "ant-design-vue/lib/notification"

// 通过Vue.use去使用这个插件
Vue.use(Antd)
// 在Vue实例中添加$notification这个变量，因为在api.js当中要使用它
Vue.$notification=Notification
// 引入ant design组件库的样式
import 'ant-design-vue/dist/antd.css'


//引入验证表单的第三方库
import VeeValidate, { Validator } from 'vee-validate';
//引入中文语言包
import zh_CN from 'vee-validate/dist/locale/zh_CN';
Vue.use(VeeValidate);
Validator.localize('zhzh_CN',zh_CN);


Vue.config.productionTip = false

new Vue({
  store,
  render: h => h(App)
}).$mount('#app')
