<template>
  <div id="student">
    <!-- columns为表格每列的配置描述 -->
    <!-- dataSource为数据数组，这个表格要渲染哪个数组的数据 -->
    <!-- pagination属性 分页器，参考配置项或 pagination文档，设为 false 时不展示和进行分页 -->
    <!-- rowKey	表格行 key 的取值，可以是字符串或一个函数 -->
    <a-table
      :rowKey="assignments=>assignments.assignment_id"
      :columns="columns"
      :dataSource="student.assignments"
      :pagination="false"
    >
      <!-- slot-scope,接收参数 -->
      <!-- text当前行的值
      record当前行的数据-->
      <template slot="operation" slot-scope="text, record">
        <a-upload
          name="file"
          :multiple="true"
          :action="uploadHost"
          :headers="{Token:info.token}"
          :data="{id:record.assignment_id}"
          @change="changeUpload"
          :showUploadList="false"
        >
          <!-- name 发到后台的文件参数名 -->
          <!-- multiple 是否支持多选文件，ie10+ 支持。开启后按住 ctrl 可选择多个文件。-->
          <!-- action 上传的地址-->
          <!-- headers 设置上传的请求头部，IE10 以上有效-->
          <!-- data	上传所需参数或返回上传参数的方法 -->
          <!-- change	上传文件改变时的状态 上传中、完成、失败都会调用这个函数。 -->
          <!-- showUploadList 是否展示 uploadList(进度条), 可设为一个对象，用于单独设定 showPreviewIcon 和 showRemoveIcon -->
          <a-button>
            <a-icon type="upload" />上传
          </a-button>
        </a-upload>
        <a v-if="record.work" :href="`${downloadHost}?id=${record.work.id}&type=student`">
          <!-- 下载作业文件，通过?传参， -->
          <a-button icon="download">下载</a-button>
        </a>
      </template>
      <template slot="info" slot-scope="text,record">
        <div v-if="record.work">
          <div v-if="record.work.status==0">
            {{record.work.teacher_download_time.slice(0,10)=="0000-00-00"?
            `${record.work.commit_time}学生提交`:
            `${record.work.teacher_download_time}老师已经下载`}}
          </div>
          <div v-else-if="record.work.status==1">
            <p>{{`${record.work.review_time}老师提交修改意见`}}</p>
            <!--使用 @click="visible=record.work" 的写法代替@click="visible=true"，用来将record.work存储到visible中-->
            <a-button @click="visible=record.work">查看详情</a-button>
          </div>
          <div v-else>
            <p>{{`${record.work.review_time}老师完成批改`}}</p>
            <a-button @click="visible=record.work">查看详情</a-button>
          </div>
        </div>
      </template>
    </a-table>
    <!-- :visible="visible"也可写成v-model="visible" -->
    <!-- visible默认为false不显示，当变为true就显示 -->
    <!-- cancel事件	点击遮罩层或右上角叉或取消按钮的回调 -->
    <a-modal :visible="Boolean(visible)" @cancel="visible=false" @ok="visible=false">
      <h4>批改意见</h4>
      <p v-if="visible&&visible.teacher_review">{{visible.teacher_review}}</p>
      <p v-if="visible&&visible.teacher_upload_name">
        <a :href="`${downloadHost}?id=${visible.id}&type=teacher`">
          <a-button>附件</a-button>
        </a>
      </p>
    </a-modal>
  </div>
</template>

<script>
import { mapState } from "vuex";

const Host = "http://sandbox_api.estudy.chanke.xyz";
const work_status = {
  "0": "待批改",
  "1": "需完善",
  "2": "已完成"
};
export default {
  // 接收来自父组件的自定义属性值
  props: ["student"],
  computed: {
    ...mapState("user", ["info"])
  },
  methods: {
    changeUpload(info) {
      // 上传时会把info信息传给我们
      // info.file.status === "done"上传完成
      // info.file.response.data是上传请求成功后，后台给我们返回的数据
      if (info.file.status === "done" && info.file.response.data) {
        // 调用vue实例中的message组件，它的info方法
        // $message组件是ant design一大堆组件中的一个组件，在vue中引入了ant design组件，所以在vue实例中可以通过$message来调用，$notification也是一样的
        this.$message.info("上传成功");
        this.$emit("loadDetails");
         
      } else {
        
        this.$notification.error({
          message: "上传失败",
          description: info.file.response.errorMessage
        });
      }
    }
  },
  data() {
    return {
      // visible 用于控制modal框显示与否
      visible: false,
      uploadHost: Host + "/student/upload",
      downloadHost: Host + "/student/download",
      // columns为表格列的配置
      columns: [
        {
          // title为表格的这列的标题
          title: "课程",
          // dataIndex为表格的这列要渲染数组中的哪个数据
          dataIndex: "org_name"
        },
        {
          title: "作业名称",
          dataIndex: "name"
        },
        {
          title: "开始-截止时间",
          // 需要进行两个数据的拼接，是数据的复杂处理，所以不用dataIndex了
          // customRender 自定义的数据展示
          // customRender函数的参数分别为当前行的值，当前行数据，行索引
          // return 返回值就是渲染展示的值
          customRender(text, record, index) {
            // text当前行的值
            // record当前行的数据
            //index当前行的索引
            return `${record.start_time} -- ${record.end_time}`;
          }
        },
        {
          title: "操作",
          // scopedSlots,通过该属性配置支持 slot-scope 的属性，如 scopedSlots: { customRender: 'XXX'}
          scopedSlots: {
            // scope的名字operation,去渲染operation这个插槽
            customRender: "operation"
          }
        },
        {
          title: "状态",
          customRender(text, record, index) {
            return record.work ? work_status[record.work.status] : "未提交";
          }
        },
        {
          title: "信息",
          scopedSlots: {
            // scope的名字operation,去渲染operation这个插槽
            customRender: "info"
          }
        }
      ]
    };
  }
};
</script>

