<template>
  <div class="authorization">
    <!-- 登录框是一个form表单 -->
    <form @submit.prevent="submit">
      <!-- 点击submit按钮会触发form表单的submit事件 -->
      <div class="field">
        <a-input
          type="text"
          placeholder="请输入用户名"
          v-validate="'required'"
          name="name"
          data-vv-as="用户名"
          v-model="form.name"
          :class="{error:errors.has('name')}"
        >
          <!-- v-validate是表单验证插件vee-validate的指令 -->
          <!-- v-validate="'required'" 后面是验证的规则，这些规则也可以自己去写，validate为我们提供了一些内置的规则，required规则表示当前的input框必须填 -->
          <!-- 一个页面可能会有很多个input框，所以给一个nane值，去定位是哪一个input框 -->
          <!-- data-vv-as="用户名",不写会使用name的值作为提示头 -->
          <!-- 判断有没有错误信息，用errors.has方法,有错误信息时，当前的input框就会有error这个类 -->
          <a-icon slot="prefix" type="user" class="icon"></a-icon>
          <!-- 为input框的前面加上一个图表 -->
          <!-- type是图标名字(ant design定义的图标) -->
        </a-input>
        <span class="error-tip">{{ errors.first('name') }}</span>
        <!-- errors这个变量,是用来存错误信息的 -->
        <!-- .first('name')方法，去获取name这个input框的错误信息 -->
      </div>
      <div class="field">
        <a-input
          type="password"
          placeholder="请输入密码"
          v-validate="'required'"
          name="password"
          data-vv-as="密码"
          v-model="form.password"
          :class="{error: errors.has('password')}"
        >
          <a-icon slot="prefix" type="lock" class="icon"></a-icon>
        </a-input>
        <span class="error-tip">{{ errors.first('password') }}</span>
      </div>
      <div class="field">
        <a-button type="primary" htmlType="submit">提交</a-button>
        <!-- type="primary"改变按钮的样式，使其变成蓝色 -->
        <!-- htmlType="submit"使button按钮变成submit按钮，submit按钮在点击使会触发form表单的submit事件 -->
      </div>
    </form>
  </div>
</template>

<script>
import api from "../utils/api";

export default {
  data: function() {
    return {
      form: {
        name: "",
        password: ""
      }
    };
  },
  methods: {
    submit() {
      // 引入validate库时，会把$validator这个变量注入进来，$validator有一个validateAll方法，它会验证里面的数据
      this.$validator.validateAll().then(result => {
        // result为true或false
        if (result) {
          // 如果result为true，说明表单通过了验证，那么就dispatch(调用)user中的actions的longin函数，并把this.form传给它
          // 当前vue实例中的$store中有dispatch方法，用来触发actions中的方法,dispatch是用来调用actions中的方法的函数
          // actions是用来存储异步调用方法的集合
          // user有独立的命名空间，所以是要触发user下的login方法
          
          // $store 是挂载在 Vue 实例上的（即Vue.prototype）
          // store是挂载到vue上，为vue的根实例

          this.$store.dispatch("user/login", this.form);
        }
      });
    }
  }
};
</script>

<style lang="scss">
.authorization {
  // 使用flex布局，让form表单居中
  display: flex;
  justify-content: center;
  form {
    width: 400px;
  }
}
.field {
  margin: 20px;
  .icon {
    color: rgba(0, 0, 0, 0.25);
  }
  // ant dedign组件的a-button标签在页面中还是会显示成button标签
  button {
    width: 100%;
  }
}
.error-tip {
  color: red;
  font-size: 12px;
}
.error {
  border: 1px solid red;
}
</style>