// 管理登录的状态的模块

import api from "../../utils/api"

const state = {
    // loggedIn判断是否登录
    loggedIn:false,
    // info用来存储登录信息
    info: {},
};
const getters = {

};
const mutations = {
    login(state,data) {
        state.loggedIn = true;
        state.info=data;
        // 为所有请求的头部加上Token
        api.defaults.headers.common["Token"]=state.info.token;
    },
    // updateToken 更新token
    updateToken(state,token){
        state.info.token=token;
    },
    // logout 登出函数
    logout(state){
        state.info={};
        state.loggedIn=false;
        // 退出时首部Token设成空
        api.defaults.headers.common["Token"]='';
    }
};
// mutations中的函数，第一个参数是state
// actions中的函数，第一个参数是context

const actions = {
    // 异步的操作放在actions当中
    login(context, form) {
        // 第一个参数context是vuex为我们传进来的环境对象，context里面有一些方法(如commit，dispatch等)
        // 第二个参数form是接受调用这个函数时传过来的参数
        api.post("/auth/login", form).then(result => {
            context.commit("login",result);
        })
    },
    // checkedLogin函数用于检验token有没有过期；
    // 使用“续期接口”,如果token过期，就报错误；如果没有过期，就自动续期
    checkedLogin(context){
        // 请求的headers需要传Token过去
        // 其它的请求也会需要token，所以使用common，设置到所有的headers上
        // context是vuex为我们传进来的环境对象,state在里面
        // $store  Vue 实例上的,当前不是vue实例，所以不能使用$store
        api.defaults.headers.common["Token"]=context.state.info.token;
        // 请求的时候，第一个参数是地址，第二个参数是传的参数，第三个参数是配置
        // _slient，一个自定义的属性，请求成功，就会传过去，请求不成功，就不会传过去
        api.post("/auth/refreshToken",{},{_slient:true}).then(token=>{
            // 成功的回调函数，会返回一个新的token(续期的token),此时去更新token
            context.commit("updateToken",token)
        },err=>{
            // 失败的回调函数，说明token过期了,此时登出
            context.dispatch("logout")
        })
    },
    logout(context){
        context.commit("logout")
    }
};

// 暴露出去
export default {
    // 在暴露的时候给一个属性namespaced。设为true，这样就使得当前的user模块有独立的作用域(命名空间)
    // 例如：还有另一个模块student，在student模块中也有一个数据loggedIn，设置namespa:true后，两个不同模块中的loggedIn就是不同的数据了
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
}